// 生成层叠结构的表格

const { Table, Row, Cell } = require('./tableElements')
const fillSchemaSize = require('../helpers/fillSchemaSize')
const retrieveProperty = require('../helpers/retrieveProperty')

// 该函数要求 data、schema 参数必须传递，并且 schema 必须是规范化的。
function generateTable (data, schema) {
  if (!('size' in schema)) {
    schema = fillSchemaSize(schema)
  }

  if (Array.isArray(data)) {
    return new Table(data.map(dataItem => generateTable(dataItem, schema)), schema.size[1])
  }

  if (Array.isArray(schema)) {
    return new Row(schema.map(schemaItem => generateTable(data, schemaItem)))
  }
  
  const value = retrieveProperty(data, schema.path)
  if (schema.props) {
    return generateTable(value, schema.props)
  }

  return new Cell({ val: value })
}

module.exports = generateTable
